import React, { Component } from 'react'
import { Link } from 'react-router-dom'
import Signedinlinks from './signedinlinks'
import Signedoutlinks from './signedoutlinks'
import {connect} from 'react-redux'

//console.log(this.props)

class Navbar extends Component{
render() {
  const {profile, details}= this.props
  const k= '/profile/' +details.uid 
  return (
    <nav className="nav-wrapper grey darken-3">
    <Link to='/' class="left"><i class="material-icons">home</i></Link>
      <div className="container">
      <Signedinlinks details={details} profile={profile} k={k}/>
      <Signedoutlinks details={this.props.details}/>
        
        </div>
    </nav>
  )}
}
const mapStatetoProps=(state)=>{
  console.log(state);
  return {
    details:state.firebase.auth,
    profile:state.firebase.profile
  }
}

export default  connect(mapStatetoProps)(Navbar)