import React, {Component} from 'react'
import { connect } from 'react-redux'
import { Link } from 'react-router-dom'
import {compose} from 'redux'
import { firestoreConnect } from 'react-redux-firebase'
import {deleteProject } from '../../store/actions/projectActions'
import {updateProject } from '../../store/actions/projectActions'
import {Redirect} from 'react-router-dom'
import ProjectReactions from './projectReaction'



class projectdetails extends Component {

    state={
      comment: false
    }

    handleClick = () => {
      if (window.confirm('Are you sure you wish to delete this item?'))
      {this.props.deleteProject(this.props.match.params.id)
      this.props.history.push('/')}
    } 
           
      render(){

        var id= (this.props.match.params.id);
        const kk= '/edit/'+id;
        const {auth}= this.props
        const a= this.props.project
        let project=false
        if(a)  project= a.find(project => (project.id  === id ));   
        
        if(auth.uid && project)
        console.log(project, 345678345678);
        if(!auth.uid) return <Redirect to='/SignIn'/>
        if(project)
        return (
            <div class="container section project-details">
            <div class="card z-depth-0">
                <div class="card-content">
                    <span class="card-title blue-text">{project.title} 
                    {
                      auth.uid===project.AuthorId ?
                      <Link to= {kk}> 
                    <div class="right btn-small butt z-depth-10"></div></Link> : null}</span>
                    <div class="row">
                    {project.image? <img src={project.image} class="col left"/>:null}
                    <p class="card col m6 right">{project.content}</p>   
                    </div>

                </div>


                <div class="card-action gret lighten-4 grey-text">
                <ProjectReactions auth= {auth} project={project} updateProject={this.props.updateProject} />
               
                Posted by {project.AuthorFirstName} {project.AuthorLastName} 
                <div>{project.createdAt.toDate().toString().slice(0,15)}</div></div>
            </div>


        
            <div className='center'>
            {auth.uid===project.AuthorId ? 
              <button className="btn white black-text z-depth-10" onClick={this.handleClick}>Delete Post</button>
               : null}
              </div>
        </div>
        )
      else return null}
    }


    const mapStateToProps = (state) => {
        return {
          project:state.firestore.ordered.project,
          auth:state.firebase.auth
        }
      }

    const mapDispatchToProps =(dispatch)=>{
      return{
        deleteProject:(id)=>{dispatch(deleteProject(id))},
        updateProject:(id,project, job,authid)=>{dispatch(updateProject(id,project, job, authid))}
      }
    }
     
    export default compose(
      firestoreConnect(() => ['project']),
      connect(mapStateToProps, mapDispatchToProps)
      )(projectdetails)
