import React from 'react'
import { Link } from 'react-router-dom'
import {connect } from 'react-redux'
import {signOut} from '../../store/actions/projectActions' 



const signedinlinks = (props) => {
  const handleClick=(props)=>{
    if (window.confirm('Are you sure you wish to Log Out?'))
    props.signOut()
  }

  if(props.details.isEmpty !== true)
  return (
      <div className="container right">
      <ul className="right">
      <li><Link to= '/Create'> New Projects</Link></li>
      <li><Link onClick={()=>handleClick(props)}> Log Out </Link></li>
      <li><Link to= '/upload'> UpDate DP</Link></li>
      <li class="pink lighten-1"><Link to= {props.k}> {props.profile.firstName} 
      <i class="material-icons right">account_circle</i>
      </Link></li>
      </ul>
        </div>
  )
  else return null
}

const mapDispatchToProps=(dispatch)=>{
  return{
    signOut: ()=> dispatch(signOut())
  }
}

export default connect(null, mapDispatchToProps)(signedinlinks);