import React from 'react'
import { Link } from 'react-router-dom';

const projectsummary = ({project}) => {
    var k ="/project/"+project.id;
    return (
      <div className="card z-depth-2 project-summary">
        <div className="card-content grey-text text-darken-3">
          <Link to={k}><span className="card-title ">{project.title}</span></Link>
          <p>{project.content.slice(0,60)}...</p>
          <p class="card-action gret lighten-4 grey-text" >Posted by {project.AuthorFirstName} Ninja
          <p>{project.createdAt.toDate().toString().slice(0,15)}</p></p>
        </div>
      </div>
    )
  }

export default projectsummary;